<template>
  <div class="flex flex-col flex-1 items-center space-y-4 mt-10">
    <i :class="`fab fa-twitter text-4xl text-primary ${loading ? 'animate-bounce' : ''}`"></i>
    <p class="text-2xl font-bold"> </p>
    <input v-model="email" type="email" class="w-96 px-4 py-3 rounded border border-gray-300 focus:ring-2 focus:border-primary" placeholder="" />
    <input v-model="password" type="password" class="w-96 px-4 py-3 rounded border border-gray-300 focus:ring-2 focus:border-primary" placeholder="" />
    <button @click="onLogin" class="w-96 rounded bg-primary text-white py-4 hover:bg-dark"></button>
    <router-link to="/register">
      <button class="text-primary"> ?  </button>
    </router-link>
  </div>
</template>

<script>
import { ref } from 'vue'
import { auth, USER_COLLECTION } from '/src/firebase'
import { useRouter } from 'vue-router'
import store from '/src/store'

export default {
  setup() {
    const email = ref('')
    const password = ref('')
    const loading = ref(false)
    const router = useRouter()

    const onLogin = async () => {
      if (!email.value || !password.value) {
        alert(',   .')
        return
      }

      try {
        loading.value = true
        const { user } = await auth.signInWithEmailAndPassword(email.value, password.value)
        const doc = await USER_COLLECTION.doc(user.uid).get()
        store.commit('SET_USER', doc.data())
        router.replace('/')
      } catch (e) {
        switch (e.code) {
          case 'auth/invalid-email':
            alert('  .')
            break
          case 'auth/wrong-password':
            alert(' .')
            break
          case 'auth/user-not-found':
            alert('   .')
            break
          default:
            alert(e.message)
            break
        }
      } finally {
        loading.value = false
      }
    }

    return { email, password, loading, onLogin }
  },
}
</script>

