<template>
  <div class="flex flex-col flex-1 items-center space-y-4 mt-10">
    <i :class="`fab fa-twitter text-4xl text-primary ${loading ? 'animate-bounce' : ''}`"></i>
    <p class="text-2xl font-bold"> </p>
    <input v-model="username" type="text" class="w-96 px-4 py-3 rounded border border-gray-300 focus:ring-2 focus:border-primary" placeholder="" />
    <input v-model="email" type="email" class="w-96 px-4 py-3 rounded border border-gray-300 focus:ring-2 focus:border-primary" placeholder="" />
    <input v-model="password" type="password" class="w-96 px-4 py-3 rounded border border-gray-300 focus:ring-2 focus:border-primary" placeholder="" />
    <button v-if="loading" class="w-96 rounded bg-light text-white py-3"> .</button>
    <button v-else @click="onRegister" class="w-96 rounded bg-primary text-white py-3 hover:bg-dark"></button>
    <router-link to="/login">
      <button class="text-primary">  ?  </button>
    </router-link>
  </div>
</template>

<script>
import { ref } from 'vue'
import { auth, USER_COLLECTION } from '/src/firebase'
import { useRouter } from 'vue-router'

export default {
  setup() {
    const email = ref('')
    const username = ref('')
    const password = ref('')
    const loading = ref(false)
    const router = useRouter()

    const onRegister = async () => {
      if (!username.value || !email.value || !password.value) {
        alert(', ,   .')
        return
      }

      try {
        loading.value = true
        const { user } = await auth.createUserWithEmailAndPassword(email.value, password.value)
        const doc = USER_COLLECTION.doc(user.uid)
        await doc.set({
          uid: user.uid,
          username: username.value,
          email: email.value,
          profile_image_url: '/vue-twitter-clone-coding/profile.jpeg',
          background_image_url: '/vue-twitter-clone-coding/background.png',
          num_tweets: 0,
          followers: [],
          followings: [],
          created_at: Date.now(),
        })
        alert('  .  .')
        router.push('/login')
      } catch (e) {
        switch (e.code) {
          case 'auth/invalid-email':
            alert('  .')
            break
          case 'auth/week-password':
            alert('  .')
            break
          case 'auth/email-already-in-use':
            alert('    .')
            break
          default:
            alert(' ')
            break
        }
        alert(e.message)
      } finally {
        loading.value = false
      }
    }

    return { username, email, password, loading, onRegister }
  },
}
</script>

